@import "_colors.scss";
@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap');

html,
body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

html {
    font-family: "Manrope", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
}

main {
    background: url("../images/sw4.png") center center no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 100vh;
}

aside#content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    min-width: 80%;

    .panel {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background: rgba(255, 255, 255, 0.8) url("data:image/png;base64,");
        background-size: 40px 40px;
        border-radius: .7rem;
        padding: 1rem 2rem;
    }
}

form#create_account {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;

    input {
        width: 100%;
        margin: .5rem 0;
        padding: .5rem;
        border: 1px solid $color-primary;
        border-radius: .5rem;
        font-size: 1.2rem;
        font-weight: 600;
        color: $color-primary;
    }

    input[type="submit"] {
        background-color: $green;
        color: $color-primary;
        font-weight: 700;
        border: none;
        border-radius: .5rem;
        padding: .5rem 1rem;
        cursor: pointer;

        &:hover {
            background-color: darken($green, 10%);
            color: $white;
        }
    }
}

div.response {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: 1rem 0;
    padding: 1rem;
    border-radius: .5rem;
    background-color: rgba(255, 255, 255, 0.8);
    color: $color-primary;
    font-weight: 600;
    font-size: 1.2rem;

    &.success {
        background-color: $green;
        color: $color-primary;
    }

    &.fail {
        background-color: $red;
        color: $color-primary;
    }
}